import axios from 'axios'
import { serverUrl } from '@/utils'

export default {
  state: {
    courses: [],
  },
  getters: {
    courses: state => state.courses,
  },
  actions: {
    getCourses({ commit }) {
      return new Promise((resolve, reject) => {
        axios.get(`${serverUrl}/api/courses`).then(response => {
          commit('setCourses', response.data)
          resolve(response)
        }).catch(err => {
          reject(err)
        })
      })
    },
    createCourses({ commit }, data) {
      return new Promise((resolve, reject) => {
        axios.post(`${serverUrl}/api/courses`, data).then(response => {
          commit('setCourses', response.data.courses)
          resolve(response)
        }).catch(err => {
          reject(err)
        })
      })
    },
    async updateCourses({ commit }, data) {
      return new Promise((resolve, reject) => {
        axios.put(`${serverUrl}/api/courses/${data.id}`, data).then(response => {
          commit('setCourses', response.data.courses)
          resolve(response)
        }).catch(err => {
          reject(err)
        })
      })
    },
  },
  mutations: {
    // eslint-disable-next-line no-return-assign
    setCourses: (state, courses) => (state.courses = courses),
  },

}
