import Vue from 'vue'
import VueRouter from 'vue-router'

import { authenticate, authenticated, studentauthenticated } from '@/auth'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    beforeEnter: authenticate,
    component: () => import('@/views/pages/Index.vue'),
    meta: {
      layout: 'blank',
    },
  },
  {
    path: '/login',
    name: 'login',
    beforeEnter: authenticate,
    component: () => import('@/views/pages/Login.vue'),
    meta: {
      layout: 'blank',
    },
  },
  {
    path: '/admin/dashboard',
    name: 'admin-dashboard',
    beforeEnter: authenticated,
    component: () => import('@/views/admin/pages/Dashboard.vue'),
  },
  {
    path: '/admin/students',
    name: 'admin-students',
    beforeEnter: authenticated,
    component: () => import('@/views/admin/pages/Students.vue'),
  },
  {
    path: '/admin/groups',
    name: 'admin-groups',
    beforeEnter: authenticated,
    component: () => import('@/views/admin/pages/Groups.vue'),
  },
  {
    path: '/admin/sections',
    name: 'admin-sections',
    beforeEnter: authenticated,
    component: () => import('@/views/admin/pages/Sections.vue'),
  },
  {
    path: '/admin/schedules',
    name: 'admin-schedules',
    beforeEnter: authenticated,
    component: () => import('@/views/admin/pages/Schedules.vue'),
  },
  {
    path: '/admin/submissions',
    name: 'admin-submissions',
    beforeEnter: authenticated,
    component: () => import('@/views/admin/pages/Submissions.vue'),
  },
  {
    path: '/admin/courses',
    name: 'admin-courses',
    beforeEnter: authenticated,
    component: () => import('@/views/admin/pages/Courses.vue'),
  },
  {
    path: '/admin/users',
    name: 'admin-users',
    beforeEnter: authenticated,
    component: () => import('@/views/admin/pages/Users.vue'),
  },
  {
    path: '/admin/capstone1milestone',
    name: 'admin-capstone1milestone',
    beforeEnter: authenticated,
    component: () => import('@/views/admin/pages/Onemilestones.vue'),
  },
  {
    path: '/admin/capstone2milestone',
    name: 'admin-capstone2milestone',
    beforeEnter: authenticated,
    component: () => import('@/views/admin/pages/Twomilestones.vue'),
  },
  {
    path: '/admin/researcharchives',
    name: 'admin-researcharchives',
    beforeEnter: authenticated,
    component: () => import('@/views/admin/pages/Researcharchives.vue'),
  },
  {
    path: '/student/dashboard',
    name: 'student-dashboard',
    beforeEnter: studentauthenticated,
    component: () => import('@/views/student/pages/Dashboard.vue'),
  },
  {
    path: '/student/group',
    name: 'student-group',
    beforeEnter: studentauthenticated,
    component: () => import('@/views/student/pages/Dashboard.vue'),
  },
  {
    path: '/student/section',
    name: 'student-section',
    beforeEnter: studentauthenticated,
    component: () => import('@/views/student/pages/Dashboard.vue'),
  },
  {
    path: '/student/schedule',
    name: 'student-schedule',
    beforeEnter: studentauthenticated,
    component: () => import('@/views/student/pages/Dashboard.vue'),
  },
  {
    path: '/student/submission',
    name: 'student-submission',
    beforeEnter: studentauthenticated,
    component: () => import('@/views/student/pages/Dashboard.vue'),
  },
  {
    path: '/typography',
    name: 'typography',
    component: () => import('@/views/typography/Typography.vue'),
  },
  {
    path: '/icons',
    name: 'icons',
    component: () => import('@/views/icons/Icons.vue'),
  },
  {
    path: '/cards',
    name: 'cards',
    component: () => import('@/views/cards/Card.vue'),
  },
  {
    path: '/simple-table',
    name: 'simple-table',
    component: () => import('@/views/simple-table/SimpleTable.vue'),
  },
  {
    path: '/form-layouts',
    name: 'form-layouts',
    component: () => import('@/views/form-layouts/FormLayouts.vue'),
  },
  {
    path: '/pages/account-settings',
    name: 'pages-account-settings',
    component: () => import('@/views/pages/account-settings/AccountSettings.vue'),
  },
  {
    path: '/pages/register',
    name: 'pages-register',
    component: () => import('@/views/pages/Register.vue'),
    meta: {
      layout: 'blank',
    },
  },
  {
    path: '/error-404',
    name: 'error-404',
    component: () => import('@/views/Error.vue'),
    meta: {
      layout: 'blank',
    },
  },
  {
    path: '*',
    redirect: 'error-404',
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})

export default router
