import axios from 'axios'
import { serverUrl } from '@/utils'

export default {
  state: {
    onemilestones: [],
  },
  getters: {
    onemilestones: state => state.onemilestones,
  },
  actions: {
    getOnemilestones({ commit }) {
      return new Promise((resolve, reject) => {
        axios.get(`${serverUrl}/api/onemilestones`).then(response => {
          commit('setOnemilestones', response.data)
          resolve(response)
        }).catch(err => {
          reject(err)
        })
      })
    },
    createOnemilestones({ commit }, data) {
      return new Promise((resolve, reject) => {
        axios.post(`${serverUrl}/api/onemilestones`, data).then(response => {
          commit('setOnemilestones', response.data.milestones)
          resolve(response)
        }).catch(err => {
          reject(err)
        })
      })
    },
    async updateOnemilestones({ commit }, data) {
      return new Promise((resolve, reject) => {
        axios.put(`${serverUrl}/api/onemilestones/${data.id}`, data).then(response => {
          commit('setOnemilestones', response.data.milestones)
          resolve(response)
        }).catch(err => {
          reject(err)
        })
      })
    },
  },
  mutations: {
    // eslint-disable-next-line no-return-assign
    setOnemilestones: (state, onemilestones) => (state.onemilestones = onemilestones),
  },

}
