import axios from 'axios'
import { serverUrl } from '@/utils'

export default {
  state: {
    twomilestones: [],
  },
  getters: {
    twomilestones: state => state.twomilestones,
  },
  actions: {
    getTwomilestones({ commit }) {
      return new Promise((resolve, reject) => {
        axios.get(`${serverUrl}/api/twomilestones`).then(response => {
          commit('setTwomilestones', response.data)
          resolve(response)
        }).catch(err => {
          reject(err)
        })
      })
    },
    createTwomilestones({ commit }, data) {
      return new Promise((resolve, reject) => {
        axios.post(`${serverUrl}/api/twomilestones`, data).then(response => {
          commit('setTwomilestones', response.data.milestones)
          resolve(response)
        }).catch(err => {
          reject(err)
        })
      })
    },
    async updateTwomilestones({ commit }, data) {
      return new Promise((resolve, reject) => {
        axios.put(`${serverUrl}/api/twomilestones/${data.id}`, data).then(response => {
          commit('setTwomilestones', response.data.milestones)
          resolve(response)
        }).catch(err => {
          reject(err)
        })
      })
    },
  },
  mutations: {
    // eslint-disable-next-line no-return-assign
    setTwomilestones: (state, twomilestones) => (state.twomilestones = twomilestones),
  },

}
