import axios from 'axios'
import { serverUrl } from '@/utils'

export default {
  state: {
    researcharchives: [],
  },
  getters: {
    researcharchives: state => state.researcharchives,
  },
  actions: {
    getResearcharchives({ commit }) {
      return new Promise((resolve, reject) => {
        axios.get(`${serverUrl}/api/researcharchives`).then(response => {
          commit('setResearcharchives', response.data)
          resolve(response)
        }).catch(err => {
          reject(err)
        })
      })
    },
    createResearcharchives({ commit }, data) {
      return new Promise((resolve, reject) => {
        axios.post(`${serverUrl}/api/researcharchives`, data).then(response => {
          commit('setResearcharchives', response.data.researches)
          resolve(response)
        }).catch(err => {
          reject(err)
        })
      })
    },
    async updateResearcharchives({ commit }, data) {
      return new Promise((resolve, reject) => {
        axios.put(`${serverUrl}/api/researcharchives/${data.id}`, data).then(response => {
          commit('setResearcharchives', response.data.researches)
          resolve(response)
        }).catch(err => {
          reject(err)
        })
      })
    },
  },
  mutations: {
    // eslint-disable-next-line no-return-assign
    setResearcharchives: (state, researcharchives) => (state.researcharchives = researcharchives),
  },

}
