import axios from 'axios'
import { serverUrl } from '@/utils'

export default {
  state: {
    users: [],
  },
  getters: {
    users: state => state.users,
  },
  actions: {
    getUsers({ commit }) {
      return new Promise((resolve, reject) => {
        axios.get(`${serverUrl}/api/users`).then(response => {
          commit('setUsers', response.data)
          resolve(response)
        }).catch(err => {
          reject(err)
        })
      })
    },
    createUsers({ commit }, data) {
      return new Promise((resolve, reject) => {
        axios.post(`${serverUrl}/api/users`, data).then(response => {
          commit('setUsers', response.data.users)
          resolve(response)
        }).catch(err => {
          reject(err)
        })
      })
    },
    async updateUsers({ commit }, data) {
      return new Promise((resolve, reject) => {
        axios.put(`${serverUrl}/api/users/${data.id}`, data).then(response => {
          commit('setUsers', response.data.users)
          resolve(response)
        }).catch(err => {
          reject(err)
        })
      })
    },
  },
  mutations: {
    // eslint-disable-next-line no-return-assign
    setUsers: (state, users) => (state.users = users),
  },

}
